.App {
  text-align: center;
  margin: 5% auto;
  width: 25%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.game {
  height: 500;
  width: 300;
}

.game .score {
  float: left;
  margin-top: 5%;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  height: 300px;
  width: 400px;
}

.card {
  display: flex;
  height: 100px;
  width: 100px;
}

.card img {
  height: 100px;
  width: 100px;
}

.footer {
  position: fixed;
  bottom: 8%;
}
